body.active-modal {
    overflow-y: hidden;
}

.project-containeur{
    background-color: #141416;
    height: 100vh;
    display: grid;
    grid-template-columns: 15% 85%;
    align-items: center;
    font-family: $font-1;
    
    .containeur-left {
        display: flex;
        justify-content: flex-end;
        color: $white-1;
        margin-left: 2rem;
        cursor: pointer;
        transition: all .3s ease;
            
            .arrow-btn {
                transition: all .3s ease;
                height: 14px;
            }

            &:hover .arrow-btn {
                transform: rotate(90deg);
                color: $color-2;
            }
    
            &:hover {
                transform: scale(1.05);
                color: $color-2;
            }
    }


    .containeur-right {
        
        @include verticalCenter;

        img {
            width: 90%;
            margin: 0 auto;
            box-shadow: 12px 12px 100px $white-1, -12px -12px 100px $white-1;
            cursor: pointer;
        }

    }

        .modal{
            position: absolute;
            width: 100%;
            align-self: center;
            height: 100%;
            overflow-y: scroll;
            top: auto;
            left: auto;
            right: auto;
            bottom: auto;

            
            .modal-content {
                // position: absolute;
                // top: 0px;
                // bottom: 0px;
                // left: 120px;
                // right: 120px;
                // text-align: center;
                // z-index: 1;
                
                img {
                    vertical-align: middle;
                    display: inline-block;
                    max-height: 100%;
                    max-width: 100%;
            }
            }
            .close-modal {
             position: absolute;
             top: 10px;
             right: 30px;
             padding: 8px 22px;
             background: none;
             color: $white-1;
             border: 2px solid $white-1;
             font-size: 1.15rem;
             font-weight: 800;
             border-radius: 6px;
             cursor: pointer;

             &:hover {
                background-color: white;
                color: #2F4050;
            }
          }
    }
}